body {
  background-color: #26272c !important;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  font-family: "PT Serif";
}
.btn {
  border-radius: 0 !important;
}
.btn-success {
  background-color: #049747 !important;
}
/* ------------------------------------
                NavBar
--------------------------------------*/
.navbar .navbar-brand {
  color: #fff;
}
.navbar .nav-link {
  color: #a0a0a0;
}
.navbar .nav-item.active .nav-link {
  color: #fff;
}
.navbar button.btn-success {
  padding: 10px 35px;
}
.navbar i.bx-cart {
  font-size: 30px;
}
/* ------------------------------------
                Section1
--------------------------------------*/
#section1 {
  background-image: url("./image/section1-bg.jpg");
}
#section1 .triangle-bottom-right {
  background: linear-gradient(to bottom right, transparent 50%, #26272c 50.5%)
      no-repeat bottom,
    /* bottom part */ linear-gradient(0deg, transparent, transparent) no-repeat
      top;
  height: 200px;
}
